import React from 'react';
import { Routes, Route } from 'react-router-dom';

import { RouterProps } from './routerContracts';
import Movie from '../view/pageMovie';
import AboutUs from '../view/aboutUs';
import Auth from '../view/form';
import GamesCatalog from 'components/gamesCatalog';
import E404 from '../components/E404';
import MoviesCatalog from 'components/moviesCatalog';

const RouterView = ({ localStore }: RouterProps) => {
  return (
    <Routes>
      <Route path="/" element={<MoviesCatalog localStore={localStore} pageName="movieLand" />} />
      <Route path="/movie/:id" element={<Movie />} />
      <Route path="/about-us" element={<AboutUs />} />
      <Route path="/sign-in" element={<Auth />} />
      <Route path="/games" element={<GamesCatalog localStore={localStore} pageName="gameLand" />} />
      <Route path="*" element={<E404 />} />
    </Routes>
  );
};

export default RouterView;
