import React, { FormEvent } from 'react';
import FormControl from 'components/formControl';
import FormCards from 'components/formCards/FormCards';
import { ICard, IFields } from './formContracts';
import { handleBlurInputFN } from '../form/formContracts';
import { DateInput, FileInput, Input, SelectInput, SwitchInput } from 'components/input';
import { SelectOption } from 'components/input/inputContracts';

import './index.css';

type FormPresentProps = {
  handleFirstInputType: () => void;
  handleSubmit: (event: FormEvent) => void;
  handleBlurInput: handleBlurInputFN;
  refForm: React.RefObject<HTMLFormElement>;
  fields: IFields;
  cards: ICard[];
  firstType: boolean;
};

const FormPresent = ({
  handleFirstInputType,
  handleSubmit,
  handleBlurInput,
  refForm,
  fields,
  cards,
  firstType,
}: FormPresentProps) => {
  return (
    <div className="form-wrapper">
      <div className="container">
        <form
          className="form"
          ref={refForm}
          onChange={handleFirstInputType}
          onSubmit={handleSubmit}
        >
          <FormControl field={fields.name}>
            <Input
              handleBlurInput={handleBlurInput}
              isError={fields.name.isError}
              inputRef={fields.name.ref as React.RefObject<HTMLInputElement>}
              name={fields.name.name}
              type={fields.name.type}
              placeholder={fields.name.placeholder as string}
            />
          </FormControl>
          <FormControl field={fields.birthday}>
            <DateInput
              handleBlurInput={handleBlurInput}
              isError={fields.birthday.isError}
              inputRef={fields.birthday.ref as React.RefObject<HTMLInputElement>}
              name={fields.birthday.name}
              type={fields.birthday.type}
            />
          </FormControl>
          <FormControl field={fields.avatar}>
            <FileInput
              handleBlurInput={handleBlurInput}
              isError={fields.avatar.isError}
              inputRef={fields.avatar.ref as React.RefObject<HTMLInputElement>}
              name={fields.avatar.name}
              type={fields.avatar.type}
              accept={fields.avatar.accept as string}
            />
          </FormControl>
          <FormControl field={fields.language}>
            <SelectInput
              handleBlurInput={handleBlurInput}
              isError={fields.language.isError}
              inputRef={fields.language.ref as React.RefObject<HTMLSelectElement>}
              name={fields.language.name}
              options={fields.language.options as SelectOption[]}
            />
          </FormControl>
          <FormControl field={fields.notifications}>
            <SwitchInput
              handleBlurInput={handleBlurInput}
              isError={fields.notifications.isError}
              inputRef={fields.notifications.ref as React.RefObject<HTMLInputElement>}
              name={fields.notifications.name}
              type={fields.notifications.type}
            />
          </FormControl>
          <FormControl field={fields.agreed}>
            <Input
              handleBlurInput={handleBlurInput}
              isError={fields.agreed.isError}
              inputRef={fields.agreed.ref as React.RefObject<HTMLInputElement>}
              name={fields.agreed.name}
              type={fields.agreed.type}
              placeholder={fields.agreed.placeholder as string}
            />
          </FormControl>
          <button type="submit" disabled={firstType}>
            submit
          </button>
        </form>
      </div>
      <FormCards cards={cards} />
    </div>
  );
};

export default FormPresent;
