import React from 'react';
import { SwitchProps } from './inputContracts';
import './switch.css';

const SwitchInput = ({ name, type, isError, handleBlurInput }: SwitchProps) => {
  return (
    <div className="switch">
      <input
        className={isError ? 'input-error toggle-button' : 'toggle-button'}
        name={name}
        onBlur={handleBlurInput}
        type={type}
        id={name}
      />
    </div>
  );
};

export default SwitchInput;
