import { IGame, IGameDetail, IGameResponse, IOrderOption } from './gameContracts';
import makeReauest from './makeReauest';

const API_KEY = 'e60b14b4174044d9880190c10c13d738';
const BASE_URL = `https://api.rawg.io/api/games`;

export const getGamesList = async (
  search = '',
  page = 1,
  orderBy: IOrderOption = '',
  limit = 9
): Promise<IGame[]> => {
  return makeReauest(
    `${BASE_URL}?key=${API_KEY}&search=${search}&ordering=${orderBy}&page=${page}&page_size=${limit}`
  ).then((response) => (response as IGameResponse).results);
};

export const getGame = async (id: number): Promise<IGameDetail> => {
  return makeReauest(`${BASE_URL}/${id}?key=${API_KEY}`);
};
