import React from 'react';
import Card from '../gameCard';
import BackDrop from 'components/backdrop';
import DialogWindow from 'components/dialogWindow/DialogWindow';

import withCatalogData from 'components/withCatalogData';

import { Developer, Genre, IGame, IGameDetail } from 'api/gameContracts';
import { getGame, getGamesList } from 'api/gameApi';

import './index.css';

type GamesCatalogProps = {
  data: IGame[] | null;
};

type GamesCatalogState = { modalIsOpen: boolean; isLoading: boolean; game: IGameDetail | null };

class CatalogMovies extends React.Component<GamesCatalogProps, GamesCatalogState> {
  gameDetail: null | IGameDetail = null;

  constructor(props: GamesCatalogProps) {
    super(props);
    this.state = { modalIsOpen: false, isLoading: false, game: null };
  }

  toggleIsModalOpen = () => this.setState({ modalIsOpen: !this.state.modalIsOpen });

  getGame = async (id: number) => {
    this.setState({ isLoading: true });
    const game = await getGame(id);
    this.setState({ game, isLoading: false });
  };

  render() {
    return (
      <div className="container">
        <div className="games-catalog">
          {this.props.data?.map((game) => (
            <Card
              getMovie={this.getGame}
              toggleIsModalOpen={this.toggleIsModalOpen}
              key={game.id}
              title={game.name}
              year={game.released}
              image={game.background_image}
              id={game.id}
            />
          )) ?? <h1 className="error__title">Nothing found</h1>}
        </div>
        {this.state.isLoading && <BackDrop open={true} />}
        {!this.state.isLoading && (
          <DialogWindow
            title={this.state.game?.name as string}
            poster={this.state.game?.background_image as string}
            developers={this.state.game?.developers as Developer[]}
            released={this.state.game?.released as string}
            metacritic={this.state.game?.metacritic as number}
            genres={this.state.game?.genres as Genre[]}
            website={this.state.game?.website as string}
            isOpen={this.state.modalIsOpen}
            handleModalOnClose={this.toggleIsModalOpen}
          />
        )}
      </div>
    );
  }
}

export default withCatalogData<IGame>(CatalogMovies, getGamesList);
