export const debounce = (
  fn: (value: string) => void,
  timeout: number
): ((value: string) => void) => {
  let timer: null | NodeJS.Timeout = null;

  return (...args) => {
    if (timer) {
      clearTimeout(timer);
    }
    timer = setTimeout(() => {
      fn(...args);
      clearTimeout(timeout);
    }, timeout);
  };
};
