import React from 'react';
import { ICard } from 'view/form/formContracts';

import './index.css';

export default function FormCard({
  name,
  birthday,
  language,
  image,
  notifications,
  agreed,
}: ICard) {
  return (
    <section role={name} className="form-card">
      <img className="form-card__img" src={window.URL.createObjectURL(image)} alt={name} />
      <div className="form-card__text-content">
        <p className="text-contnent__item p-20">Name is: {name}</p>
        <p className="text-contnent__item p-20">Birthday: {birthday}</p>
        <p className="text-contnent__item p-20">Language: {language}</p>
        <p className="text-contnent__item p-20">Notifications are: {notifications}</p>
        <p className="text-contnent__item p-20">Agreed to share personal information: {agreed}</p>
      </div>
    </section>
  );
}
