import React, { FocusEvent, FormEvent } from 'react';
import getInitialFieldsData from './getFields';
import {
  FieldOption,
  FormContainerProps,
  FormContainerState,
  IFieldData,
  IFields,
} from './formContracts';
import FormPresent from './FormPresentation';

import './index.css';

class FormContaner extends React.Component<FormContainerProps, FormContainerState> {
  refForm = React.createRef<HTMLFormElement>();

  constructor(props: FormContainerProps) {
    super(props);
    this.state = { fields: getInitialFieldsData(), firstType: true, cards: [] };
  }

  componentDidMount(): void {
    const newFields = { ...this.state.fields };
    Object.values(newFields).forEach((field) => {
      const ref = React.createRef<HTMLInputElement>();
      field.ref = ref;
    });
    this.setState({ fields: newFields });
  }

  updeteAgreedField = (field: IFieldData) => {
    const isChecked = (field.ref as React.RefObject<HTMLInputElement>).current?.checked;
    field.value = isChecked ? 'on' : 'off';
    field.isError = !isChecked;
  };

  updeteNotificationsField = (field: IFieldData) => {
    const isChecked = (field.ref as React.RefObject<HTMLInputElement>).current?.checked;
    field.value = isChecked ? 'on' : 'off';
  };

  updateFields = (newFields: IFields) => {
    this.updeteAgreedField(newFields.agreed);
    this.updeteNotificationsField(newFields.notifications);

    Object.values(newFields).forEach((field) => {
      if (field.name !== 'agreed' && field.name !== 'notifications') {
        field.value = field.ref?.current?.value ?? '';
        field.isError = !field.regexp.test(field.value as string);
      }
    });
    this.setState({ fields: newFields });
  };

  isFormCorrect = () => Object.values(this.state.fields).every((field) => !field.isError);

  handleSubmit = (e: FormEvent) => {
    e.preventDefault();

    const newFields = { ...this.state.fields };
    this.updateFields(newFields);

    if (this.isFormCorrect()) {
      const images = (this.state.fields.avatar.ref as React.RefObject<HTMLInputElement>).current
        ?.files;
      const image = images?.length ? images[0].slice(0) : null;
      const cards = [
        ...this.state.cards,
        {
          name: this.state.fields.name.value as string,
          birthday: this.state.fields.birthday.value as string,
          image: image as Blob,
          notifications: this.state.fields.notifications.value as string,
          agreed: this.state.fields.agreed.value as string,
          language: this.state.fields.language.value as string,
        },
      ];

      this.setState({ cards });
      this.refForm.current?.reset();
    }
  };

  handleBlurInput = (e: FocusEvent<HTMLInputElement | HTMLSelectElement, Element>) => {
    const option = e.target.name as FieldOption;
    if (!this.state.fields[option].isError) {
      return;
    }
    const newFields = { ...this.state.fields };
    this.updateFields(newFields);
  };

  handleFirstInputType = () => {
    if (!this.state.firstType) {
      return;
    }
    this.setState({ firstType: false });
  };

  render(): React.ReactNode {
    return (
      <FormPresent
        handleBlurInput={this.handleBlurInput}
        handleSubmit={this.handleSubmit}
        handleFirstInputType={this.handleFirstInputType}
        fields={this.state.fields}
        cards={this.state.cards}
        firstType={this.state.firstType}
        refForm={this.refForm}
      />
    );
  }
}

export default FormContaner;
