import React from 'react';
import { InputProps } from './inputContracts';

const Input = ({ placeholder, name, type, inputRef, isError, handleBlurInput }: InputProps) => {
  return (
    <>
      <input
        className={isError ? 'input-error form-input' : 'form-input'}
        role={type}
        onBlur={handleBlurInput}
        ref={inputRef}
        id={name}
        name={name}
        type={type}
        placeholder={placeholder}
      />
    </>
  );
};

export default Input;
