import React from 'react';
import { FileInputProps } from './inputContracts';

const FileInput = ({ name, type, accept, inputRef, isError, handleBlurInput }: FileInputProps) => {
  return (
    <>
      <input
        role={type}
        onBlur={handleBlurInput}
        ref={inputRef}
        className={isError ? 'input-error form-input' : 'form-input'}
        id={name}
        type={type}
        accept={accept}
        name={name}
      />
    </>
  );
};

export default FileInput;
