import makeReauest from './makeReauest';
import { IMovieFull, IMovieShort } from './movieContracts';

const API_KEY = 'wConhx1APyKWJjs5hwz8bTBPGg1SGyR0uznuZtPP';
const BASE_URL = `https://api.watchmode.com/v1/`;

export const getMoviesList = async (selector: string): Promise<IMovieShort[]> => {
  return makeReauest(
    `${BASE_URL}autocomplete-search/?apiKey=${API_KEY}&search_value=${selector}`
  ).then((response) => response.results);
};

export const getMovie = async (id: string): Promise<IMovieFull> => {
  return makeReauest(
    `${BASE_URL}title/${id}/details/?apiKey=${API_KEY}&append_to_response=sources`
  );
};
