import React from 'react';
import { SelectInputProps } from './inputContracts';

const SelectInput = ({ name, options, inputRef, isError, handleBlurInput }: SelectInputProps) => {
  return (
    <>
      <select
        className={isError ? 'input-error form-input' : 'form-input'}
        onBlur={handleBlurInput}
        ref={inputRef}
        defaultValue="en"
        name={name}
        id={name}
      >
        {options.map((option) => (
          <option key={option.value} value={option.value}>
            {option.optionInnerText}
          </option>
        ))}
      </select>
    </>
  );
};

export default SelectInput;
