import React from 'react';
import { Button } from '@mui/material';

import './index.css';

export type GameCardProps = {
  title: string;
  year: number | string;
  image: string;
  id: number;
  getMovie: (id: number) => Promise<void>;
  toggleIsModalOpen: () => void;
};

const GameCard = ({ title, year, image, id, getMovie, toggleIsModalOpen }: GameCardProps) => {
  const handleOnCardClick = () => {
    getMovie(id);
    toggleIsModalOpen();
  };

  return (
    <div data-testid="gameCard" className="gameCard" onClick={handleOnCardClick}>
      <img className="gameCard__image" src={image} alt={title} />
      <h1 className="gameCard__title">{title}</h1>
      <div className="gameCard__descrep">{year}</div>
      <Button variant="outlined">Read more</Button>
    </div>
  );
};

export default GameCard;
