import Backdrop from 'components/backdrop';
import Search from 'components/search';
import { debounce } from 'helpers';
import { PageOption } from 'localStorage/localStorage';
import React from 'react';
import LocalStore from '../../localStorage';
import LocalStorage from '../../localStorage';

import './index.css';

type ComponentWithDataProps = { pageName: PageOption; localStore: LocalStorage };

type ComponentWithDataState<T> = {
  data: T[] | null;
  isOpen: boolean;
};

function withCatalogData<T>(
  Component: React.ElementType,
  getData: (selector: string) => Promise<T[]>
) {
  return class extends React.Component<ComponentWithDataProps, ComponentWithDataState<T>> {
    localStore: LocalStore;
    searchValue = '';

    constructor(props: ComponentWithDataProps) {
      super(props);
      this.localStore = this.props.localStore;

      let searchValue = this.localStore.getValue(props.pageName);

      if (!searchValue) {
        searchValue = 'The lord of the rings';
      }

      this.state = { data: null, isOpen: false };
      this.searchValue = searchValue;
    }

    componentDidMount(): void {
      this.updateData(this.searchValue);
    }

    componentWillUnmount(): void {
      this.localStore.updateValue(this.searchValue, this.props.pageName);
    }

    updateData = (searchValue: string) => {
      this.searchValue = searchValue;

      if (!searchValue.length) {
        this.setState({ data: null });
        return;
      }

      this.setState({ isOpen: true });

      getData(searchValue)
        .then((result) => {
          const data = result.length ? result : null;
          this.setState({ data, isOpen: false });
        })
        .catch((err) => {
          throw new Error(err);
        });
    };

    updateDataLazy = debounce(this.updateData, 600);

    handleSearchChange = (searchValue: string) => {
      this.updateDataLazy(searchValue);
    };

    render(): React.ReactNode {
      return (
        <main>
          <h1 className="catalog__title">
            {this.props.pageName === 'movieLand' ? 'MovieLand' : 'GameLand'}
          </h1>
          <Search onChange={this.handleSearchChange} value={this.searchValue} />
          {this.state.isOpen ? <Backdrop open={true} /> : <Component data={this.state.data} />}
        </main>
      );
    }
  };
}

export default withCatalogData;
