import ErrMsg from 'components/errMessage';
import React from 'react';
import { IFieldData } from 'view/form/formContracts';

type FormControlProps = { children: React.ReactNode; field: IFieldData };

const FormControl = ({ field, children }: FormControlProps) => {
  const className = field.name === 'agreed' ? 'agreed' : '';

  return (
    <div className="form-item" data-testid={field.name}>
      <div className={`${className} input-wrapper`}>
        <label htmlFor={field.name}>{field.label}</label>
        {children}
        <ErrMsg isError={field.isError} errMsg={field.errMsg} />
      </div>
    </div>
  );
};

export default FormControl;
