import React from 'react';
import { Modal } from 'antd';
import { Developer, Genre } from 'api/gameContracts';
import { CardMedia, Link } from '@mui/material';

import './index.css';

type DialogWindowProps = {
  title: string;
  poster: string;
  released: string;
  developers: Developer[];
  metacritic: number;
  genres: Genre[];
  website: string;
  isOpen: boolean;
  handleModalOnClose: () => void;
};

const DialogWindow = ({
  title,
  poster,
  released,
  developers,
  metacritic,
  genres,
  website,
  isOpen,
  handleModalOnClose,
}: DialogWindowProps) => {
  return (
    <Modal title={title} open={isOpen} onOk={handleModalOnClose} onCancel={handleModalOnClose}>
      <CardMedia component="img" height="194" image={poster} alt={title} />
      <p>Released: {released}</p>
      <p>{released}</p>
      <p>Metacritic: {metacritic}</p>
      <div>
        <hr />
        <h1>Genres</h1>
        {genres?.map((genre) => (
          <p key={genre.id}>{genre.name}</p>
        ))}
        <hr />
      </div>
      <div>
        <h1>Developers: </h1>
        {developers?.map((developer) => (
          <p key={developer.id}>{developer.name}</p>
        ))}
        <hr />
      </div>
      <Link href={website}>Website</Link>
    </Modal>
  );
};

export default DialogWindow;
