export type PageOption = 'movieLand' | 'gameLand';

type SearchValue = { searchValue: string | null };

interface ILocalState {
  pages: Record<PageOption, SearchValue>;
}

interface ILocalStore {
  ls: ILocalState;
  getValue: (options: PageOption) => string | null;
  updateValue: (value: string, option: PageOption) => void;
}

export default class LocalStore implements ILocalStore {
  ls: ILocalState;

  constructor() {
    const localStore = localStorage.getItem('React-app');
    const ls = localStore
      ? JSON.parse(localStore)
      : { pages: { movieLand: { searchValue: null }, gameLand: { searchValue: null } } };

    this.ls = ls;
  }

  get getValue() {
    return (option: PageOption) => this.ls.pages[option].searchValue;
  }

  updateValue = (value: string, option: PageOption) => {
    this.ls.pages[option].searchValue = value;
    localStorage.setItem('React-app', JSON.stringify(this.ls));
  };
}
