import React from 'react';
import { DateInputProps } from './inputContracts';

const DateInput = ({ name, type, inputRef, isError, handleBlurInput }: DateInputProps) => {
  const now = new Date();
  return (
    <>
      <input
        role={type}
        onBlur={handleBlurInput}
        ref={inputRef}
        className={isError ? 'input-error form-input' : 'form-input'}
        id={name}
        name={name}
        type={type}
        min="1900-01-01"
        max={`2022-10-${now.getDate()}`}
      />
    </>
  );
};

export default DateInput;
