import React from 'react';
import { WithRouterProps } from 'router/routerContracts';
import withRouter from '../highOrderComponents/WithRouter';
import { HeaderProps } from './headerContracts';
import HeaderPresent from './HeaderPresent';
import './index.css';

const generatePath = (location: string): string[] => {
  const path = [];
  if (location === '/') {
    path.push('catalog');
  } else if (location === '/games') {
    path.push('games');
  } else if (location === '/about-us') {
    path.push('about us');
  } else if (/\/movie\/[a-z]+[0-9]+/.test(location)) {
    path.push('catalog', 'movie');
  } else if ('/sign-in' === location) {
    path.push('sign-in');
  }
  return path;
};

const Header = ({ router }: WithRouterProps & HeaderProps) => {
  const location = router.location.pathname;
  const path = generatePath(location);
  return <HeaderPresent path={path} />;
};

export default withRouter<HeaderProps>(Header);
