import React from 'react';
import { IMovieFull } from 'api/movieContracts';
import { getMovie } from 'api/movieApi';
import E404 from 'components/E404';
import PagePresent from './PagePresentation';
import { WithRouterProps } from 'router/routerContracts';
import withRouter from 'components/highOrderComponents/WithRouter';
import Backdrop from '../../components/backdrop';

type PageProps = Record<string, string>;

type PageState = { movie: Partial<IMovieFull> | null; open: boolean };

class MoviePage extends React.Component<WithRouterProps & PageProps, PageState> {
  constructor(props: WithRouterProps & PageProps) {
    super(props);
    this.state = { movie: {}, open: true };
  }

  componentDidMount(): void {
    this.updateMovie();
  }

  updateMovie(): void {
    const { id } = this.props.router.params;

    getMovie(id ?? '')
      .then((movie) => {
        this.setState({ movie, open: false });
      })
      .catch(() => {
        this.setState({ movie: null, open: false });
      });
  }

  render() {
    return !this.state.movie ? (
      <E404 />
    ) : !Object.values(this.state.movie).length ? (
      <Backdrop open={this.state.open} />
    ) : (
      <PagePresent movie={this.state.movie as Required<IMovieFull>} />
    );
  }
}

export default withRouter<PageProps>(MoviePage);
